.carousel-nav label {
  display: inline-block;
  margin: 0 6px;
  width: 15px;
  height: 15px;
  border: 2px solid #e6ecf8;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: background-color 0.25s;
  transition: background-color 0.25s;
}

.carousel-slides {
  width: 100%;
  overflow: hidden;
}

.carousel-slides .carousel-inner {
  width: 500%;
  -webkit-transition: margin 0.5s;
  transition: margin 0.5s;
}

.carousel-slides .carousel-item {
  float: left;
  width: 20%;
}

#carousel-1:checked ~ .carousel-nav label[for='carousel-1'] {
  background-color: #141c3a;
  border: 2px solid #141c3a;
}

#carousel-2:checked ~ .carousel-nav label[for='carousel-2'] {
  background-color: #141c3a;
  border: 2px solid #141c3a;
}

#carousel-3:checked ~ .carousel-nav label[for='carousel-3'] {
  background-color: #141c3a;
  border: 2px solid #141c3a;
}

#carousel-4:checked ~ .carousel-nav label[for='carousel-4'] {
  background-color: #141c3a;
  border: 2px solid #141c3a;
}

#carousel-5:checked ~ .carousel-nav label[for='carousel-5'] {
  background-color: #141c3a;
  border: 2px solid #141c3a;
}

#carousel-1:checked ~ .carousel-slides .carousel-inner {
  margin-left: 0%;
}

#carousel-2:checked ~ .carousel-slides .carousel-inner {
  margin-left: -100%;
}

#carousel-3:checked ~ .carousel-slides .carousel-inner {
  margin-left: -200%;
}

#carousel-4:checked ~ .carousel-slides .carousel-inner {
  margin-left: -300%;
}

#carousel-5:checked ~ .carousel-slides .carousel-inner {
  margin-left: -400%;
}

